import { HttpClient, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class RFQApplicationApplicationsService {
  constructor(private http: HttpClient) {}

  getRFQApplications(): Observable<any> {
    const RFQApplicationUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/all`;
    return this.http.get<any[]>(RFQApplicationUrl);
  }

  getRFQApplicationById(params: any): Observable<any> {
    const RFQAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/fetchById`;
    return this.http.get<any>(RFQAnalysisUrl, { params });
  }
  getRFQApplicationByTheId(params: any): Observable<any> {
    const RFQAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/find-by-id`;
    return this.http.get<any>(RFQAnalysisUrl, { params });
  }
  getRFQApplicationByCode(params: any): Observable<any> {
    const RFQAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/fetchByrfqApplicationCode`;
    return this.http.get<any>(RFQAnalysisUrl, { params });
  }

  // allocateApplicationScores(params: any): Observable<any> {
  //   const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/provide/score`;
  //   return this.http.put<any>(updateBillUrl, { params });
  // }

  nominateSupplierByCodes(params: any): Observable<any> {
    const RFQApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/nominate`;
    return this.http.put<any[]>(RFQApplicationssUrl, {}, { params });
  }

  //
  getRFQApplicationByRfqCode(params: any): Observable<any> {
    const RFQApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/fetchByRfqCode`;
    return this.http.get<any[]>(RFQApplicationssUrl, {params});
  }

  getRFQApplicationByStatus(params: any): Observable<any> {
    const RFQApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/fetchByStatus`;
    return this.http.get<any[]>(RFQApplicationssUrl, { params });
  }

  deleteRFQsTemporarily(id: any): Observable<any> {
    const RfqUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/delete`;
    return this.http.delete<any>(RfqUrl, id);
  }


  postRfqApplicationScores(params: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/provide/score`;
    return this.http.put<any>(updateBillUrl, {}, {params})
  }

  getApplicationByFinancialRanking(params: any): Observable<any> {
    const ApplicationMatrixUrl = `${environment.baseUrlProcurement}/api/v1/RFQsApplication/analyse`;
    return this.http.put<any>(ApplicationMatrixUrl,{},{params});
  }
  
  // ********************************************************************************************************************
}
